import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/enhance',
  },

  {
    path: '/weapon',
    component: () => import('../layout/index.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/weapon/index.vue'),
        meta: { title: '武器' },
      },
    ],
  },

  {
    path: '/armor',
    component: () => import('../layout/index.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/armor/index.vue'),
        meta: { title: '防具' },
      },
    ],
  },

  {
    path: '/simulator',
    component: () => import('../layout/index.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/simulator/index.vue'),
        meta: { title: '配装' },
      },
      // {
      //   path: 'plan',
      //   component: () => import('../views/simulator/plan/index.vue'),
      //   meta: { title: '方案' },
      // },
    ],
  },

  {
    path: '/enhance',
    component: () => import('../layout/index.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/enhance/index.vue'),
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
